.list {
  $self: &;
  margin-bottom: 30px;

  &__add-button {
    svg,
    span {
      opacity: 0.4;
      transition: all 0.5s;
    }
    &:hover {
      svg,
      span {
        opacity: 1;
      }
    }
  }

  li {
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 10px 12px;

    &:hover #{$self}__remove-icon {
      opacity: 0.2;
    }

    #{$self}__remove-icon {
      opacity: 0;
      transition: all 0.2s;

      &:hover {
        opacity: 0.8;
      }
    }

    span {
      flex: 1;
      text-overflow: ellipsis;
      overflow: hidden;
      width: 160px;
      white-space: nowrap;
    }

    &:hover {
      background: rgba(255, 255, 255, 0.5);
    }

    &.active {
      background: #ffffff;
      box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.04);
      border-radius: 4px;
    }

    .badge {
      position: relative;
      left: 5px;
    }

    i {
      margin-right: 6px;
      display: inline-flex;
      svg {
        path {
          fill: #80806a;
        }
      }
    }
  }
}
